body {
    font-family: monospace;
    background-color: rgb(7, 9, 16);
    color: rgb(179, 176, 176);
}

.pathGroup {
    margin-top: 30px;
}

button,
.link {
    background-color: rgb(7, 9, 16);
    color: rgb(179, 176, 176);
    border: none;
    padding: 7px;
    margin: 0 10px;
    cursor: pointer;
    font-weight: bold;

}

.data-group,
.pathGroup,
.link-con {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.pathTitle {
    text-align: center;
    color: rgb(2, 163, 83);
}

.home-name {
    text-align: center;
    height: 400px;
    font-size: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color:rgb(179, 176, 176);
}

.name {
    color: rgb(2, 163, 83);
}

.dataContainer {
    width: 250px;
    border: 1px solid rgb(179, 176, 176);
    padding: 2px 20px;
    margin: 20px 10px;
    margin-top: 0;
    border-radius: 5px;
}

.dataControllers {
    display: flex;
    justify-content: center;
}

.dataControllers button {
    background-color: inherit;
    border: none;
    color: red;
    padding: 0;
    margin-bottom: 10px;
}

a, .link {
    color: #58a6ff;
    text-decoration: none;
}